  overflow: scroll;
}

.no-favorites {
  color: yellow;
  font-size: 2em;
  margin-top: 150px;
}
.Crawler {
  color: yellow;
  background: repeat url(https://thumbs.gfycat.com/AccurateNimbleBighornedsheep.webp);
  padding: 15px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  height: 1200px;
  -webkit-perspective: 400px;
          perspective: 400px;
  font-size: 500%;
  font-weight: bold;
  letter-spacing: 6px;
  line-height: 150%;
  text-align: justify;
}

.subtext {
  text-align: center;
}

.crawl-text {
  position: relative;
  top: -100px;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  -webkit-animation: crawl 45s linear;
          animation: crawl 45s linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.fade {
  position: absolute;
  width: 100%;
  min-height: 30vh;
  top: 210px;
  background-image: -webkit-gradient(linear, left bottom, left top, from(transparent), color-stop(90%, black));
  background-image: -webkit-linear-gradient(bottom, transparent, black 90%);
  background-image: linear-gradient(0deg, transparent, black 90%);
  z-index: 1;
}

@-webkit-keyframes crawl {
  0% {
    top: 300px;
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0);
  }

  100% {
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px);
  }

}

@keyframes crawl {
  0% {
    top: 300px;
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0);
  }

  100% {
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px);
  }

}
